import React from 'react';
import Navbar from './components/navbar';
import News, {Bodies} from './components/news';
import {Link, Route, Switch} from 'react-router-dom';
import Cast from './components/cast';
import './css/App.css'

interface NewsArticleProps{
	date: string,
	title: string
}

const NewsArticle: React.FC<NewsArticleProps> = (props) => {
	return (
		<div className = "news-article">
			<h5 className = "article-date">{props.date}</h5>
			<article className = "article-body">{props.title}</article>
		</div>
	);
}

interface EmbedTwitterProps {
	onMobile: boolean
}

const EmbedTwitter: React.FC<EmbedTwitterProps> = (props) => {
	const [loadedWidgets, setLoadedWidgets] = React.useState(false);
	React.useEffect(() => {
		if(!loadedWidgets&&!props.onMobile){
			const elm: HTMLScriptElement = document.createElement("script");
			elm.setAttribute("src", "https://platform.twitter.com/widgets.js");
			document.body.appendChild(elm);
			setLoadedWidgets(true);
		}
	}, [props.onMobile, loadedWidgets]);

	if(props.onMobile)return <></>;
	return (
		<div className = "twitter-embed">
			<a className="twitter-timeline" href="https://twitter.com/2020TK3BHR?ref_src=twsrc%5Etfw">Tweets by 2020TK3BHR</a>
		</div>
	)
}

const Preparing: React.FC = () => {
	return (
		<div className = "main-container">
			<div className = "pending-contents">
				このページはただいま準備中です。公開までお待ちください。
			</div>
		</div>
	)
}

const NotFound: React.FC = () => {
	return (
		<div className = "main-container">
			<div className = "pending-contents">
				Not Found
			</div>
		</div>
	)
}

const App: React.FC = () => {
	const [onMobile, setOnMobile] = React.useState(false);
	const useragent = window.navigator.userAgent.toLowerCase();
	React.useEffect(() => {
		if(!onMobile && useragent.indexOf("mobile") && (useragent.indexOf("iphone") > 0 || useragent.indexOf("ipod") > 0 || useragent.indexOf("android") > 0)){
			setOnMobile(true);
		}
	},[onMobile, useragent]);

	const [style, setStyle] = React.useState({});

	React.useEffect(() => {
		if(onMobile) setStyle({width: "100%"});
		else setStyle({width: "calc(75% - 20px)"});
	}, [onMobile]);

	const Ret: React.FC = () => {
		return (
			<>
				<Navbar />
				<div className = "main">
					<Switch>
						<Route path = "/" exact>
							<img className = "main-logo" src = "img/main-logo.png" alt = "rivoluzione"/>
							<div className = "main-container">
								<div className = "top-contents">
									<h1 className = "description-title">Story</h1>
									<div className = "description">
										<p>時は 2050 年。かつて自由な学校と呼ばれた筑駒では、自治会の権力によって生徒たちが厳しく統制されていた。</p>
										<p>そんなある日、自治会による一つの決定をきっかけに、高校 2 年生 星野・松下・本田 の 3 人たちは「筑駒革命軍」を結成する。</p>
										<p>彼らの目的は自治会を倒し "筑駒の自由" を取り戻すこと。</p>
										<p>今、自由をかけた彼らの闘いが始まる…</p>
									</div>
								</div>
								<EmbedTwitter onMobile = {onMobile}/>
								<div className = "main-contents" style = {style}>
									<Link to = "/news">
										<div className = "contents">
											<h1 className = "contents-title">News</h1>
											<NewsArticle date = {Bodies[3][0]} title = {Bodies[3][1]} />
											<NewsArticle date = {Bodies[2][0]} title = {Bodies[2][1]} />
											<NewsArticle date = {Bodies[1][0]} title = {Bodies[1][1]} />
											<NewsArticle date = {Bodies[0][0]} title = {Bodies[0][1]} /> 
										</div>
									</Link>
									<Link to = "/movie">
										<div className = "contents contents-half contents-left">
											<h1 className = "contents-title">Movie</h1>
											<img src = "img/dummy.png" width = "100%" alt = "Thumbnail of the movie"/>
											<p>
												映画本体のページです。
											</p>
										</div>
									</Link>
									<Link to = "/cast">
										<div className = "contents contents-half">
											<h1 className = "contents-title">Cast</h1>
											<img src = "img/dummy.png" width = "100%" alt = "The information of the cast"/>
											<p>
												出演するキャストの情報です。
											</p>
										</div>
									</Link>
								</div>
							</div>
						</Route>
						<Route path = "/news" component = {News} />
						<Route path = "/movie">
							<div className = "main-container main-movie">
								<h1 className = "movie-title">予告編</h1>
								<div className = "movie-wrapper">
									<iframe className = "movie-embed" title = "trailer" src = "https://www.youtube.com/embed/7lMM7fo5bf4" frameBorder = "0" allow = "accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen />
								</div>
							</div>
						</Route>
						<Route path = "/cast" component = {Cast} />
						<Route component = {NotFound} />
					</Switch>
				</div>
			</>
		);
	}
	return <Ret />
}

export default App;
